@import url(https://fonts.googleapis.com/css2?family=Kanit:wght@300&display=swap);
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: 'Kanit', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  max-width: 1000px;
  margin: 0 auto;
  background-color: #083358;
  color: white;
}

a{
  text-decoration: none;
  color: white;
  counter-reset: unset;
}

.win-card{
  border-radius: 10px;
  border: 2px solid black;
  background-color: #0da574;
}
.lose-card{
  border-radius: 10px;
  border: 2px solid black;
  background-color: #ff7e67;
}

.App {
  text-align: left;
  padding: 10px;
}


.MatchDetailed{
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 10px;
    margin-bottom: 10px;
}
.match-details{
    margin: 8px;
}
.additional-details{
    text-align: end;
    
}

.additional-details p{
    margin-bottom: 8px;
}
.MatchSmall{
    padding: 8px;
    max-height: 200px;
}
.TeamPage{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    
    grid-column-gap: 16px;
    
    -webkit-column-gap: 16px;
    
            column-gap: 16px;
    grid-row-gap: 16px;
    row-gap: 16px;
}

.team-name-section{
    grid-column: span 3;
    justify-self: start;
    align-self: end;
    font-size: 180% ;
    /* max-height: 100px; */
}

.win-loss-section{
    align-self: center;
    padding: 35px;
    text-align: center;
    /* max-height: 100px; */
}

.latest-match-section{
    grid-column: span 4;
}

.more-link{
    align-self:flex-start;
    justify-self: self-start;
}
.MatchPage{
    display: grid;
    grid-template-columns: 1fr 5fr;
    grid-column-gap: 16px;
    -webkit-column-gap: 16px;
            column-gap: 16px;
}

.team-name-year{
    text-align: center;
    margin: 20px;
    font-size: 250%;
}
.match-section{
    margin: 10px;
}
.year-selector-section{
    position: fixed;
    align-self: top;
}
.MatchTile{
    align-items: center;
    text-align: center;
    border: 3px solid black;
    border-radius: 8px;
    padding: 30px;
    margin: 20px;
    background-color:  #00AAA0;
    font-size: 130%;
}
.HomePage{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
            column-gap: 10px;
}
.heading{
    text-align: center;
    margin-bottom: 20px;
    font-size: 250%;
}

